@use "../../scss/utils/variables" as *;

.image__banner {
  position: relative;
  margin-bottom: 2rem;
  .btn {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 5rem;
    background-color: transparent;
    color: $color-tertiary;
    font-size: 3rem;
    cursor: pointer;
    border-width: 0;
    &:active,
    &:focus {
      outline: none;
    }
    &.btn-next {
      right: 0;
    }
    &.btn-previous {
      left: 0;
    }
  }
  .slide-counter {
    position: absolute;
    color: $color-tertiary;
    border-radius: 0 0 0 1rem;
    width: 100%;
    bottom: 1rem;
    text-align: center;
  }
  .image__container {
    width: 100%;
    height: 415px;
    position: relative;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      inset: 0;
      object-fit: cover;
      border-radius: 1rem;
      box-shadow: 0px 8px 24px rgb(0, 0, 0);
      opacity: 0;
      transition: opacity 0.5s ease-in-out;
      &.show {
        opacity: 1;
      }
    }
    .banner_about{
      height: 223px;
    }
    @media (max-width: 375px) {
      height: 255px;
    }
  }
}
@media screen and (max-width: 768px) {
  .slide-counter {
    display: none;
  }
}
#image__container--about{
  height: 223px;
}