@use "../../scss/utils/variables" as *;

.apartment {
  border-radius: 1rem;
  flex-basis: 30%;
  display: grid;
  align-items: end;
  width: 100%;
  height: 100%;
  background-size: cover;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1rem;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    filter: brightness(0.8);
  }
  @media screen and (max-width: 768px) {
    .apartment {
      flex-basis: 100%;
    }
  }
  & &__subtitle {
    width: 50%;
    margin-inline-start: 0.5rem;
    margin-block-end: 0.5rem;
    color: $color-tertiary;
    position: absolute;
  }
}